* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.card-min-info{
display: flex;
align-items: center;
}

.title-card-min{
   font-size: 20px;
   font-weight: 500;
   margin-left: 10px;
}

.text-card{
   font-size: 20px;
   font-weight: 500;
}

.text-card-name{
font-size: 20px;
font-weight: 300;

}
